/**
 * 
 * @param name
 * @returns {string}
 */
export function splitUrlParams(url, name) {
    var reg = new RegExp("(^|&|\\?)" + name + "=([^&]*)(&|$)", "i");
    var param = window.location.href.substring(1).match(reg);
    if (param !== null) {
        return url.replace(param[0], '?');
    } else {
        return undefined;
    }
};

/**
 * js sdk 
 * @returns {object}
 */
const hideMenus = ['menuItem:share:qq', 'menuItem:share:weiboApp', 'menuItem:favorite', 'menuItem:share:facebook', 'menuItem:share:QZone', 'menuItem:editTag', 'menuItem:delete', 'menuItem:copyUrl', 'menuItem:originPage', 'menuItem:readMode', 'menuItem:openWithQQBrowser', 'menuItem:openWithSafari', 'menuItem:share:email', 'menuItem:share:brand'];
export function wxshare(url, img_url, title) {
    getConfig(config => {
        gettoken().then(function(token) {
            Vue.http.get(config.jssdk.wxsdk + '?token=' + token + '&url=' + window.location.href).then(res => {
                res = res.data;
                if (res.errcode === 0) {
                    wx.config({
                        //jsapi
                        beta: true,
                        debug: false,
                        appId: res.data.appid,
                        timestamp: res.data.timestamp,
                        nonceStr: res.data.nonceStr,
                        signature: res.data.signature,
                        jsApiList: [
                            'onMenuShareTimeline',
                            'hideMenuItems',
                            'onMenuShareAppMessage'
                        ]
                    });
                    //wx.config
                    wx.error(function(res) { console.log(res) });
                    wx.ready(function() {
                        wx.hideMenuItems({ menuList: hideMenus });
                        wx.onMenuShareTimeline({
                            title: title, // 
                            link: url, // 
                            imgUrl: img_url, // 
                            desc: ' '
                        });
                        wx.onMenuShareAppMessage({
                            title: title, // 
                            link: url, // 
                            imgUrl: img_url, // 
                            desc: ' '
                        });
                    });
                }
            }, err => {
                reject()
            })
        })
    })
}

// Date Date String
// (M)(d)(h)(m)(s)(q)  1-2  
// (y) 1-4 (S) 1 ( 1-3 ) 
//  
// (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423 
// (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18 
Date.prototype.Format = function(fmt) { //author: meizz 
    var o = {
        "M+": this.getMonth() + 1, // 
        "d+": this.getDate(), // 
        "h+": this.getHours(), // 
        "m+": this.getMinutes(), // 
        "s+": this.getSeconds(), // 
        "q+": Math.floor((this.getMonth() + 3) / 3), // 
        "S": this.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

/**
 * 
 */
export function getUrlParams(name) {
    var reg = new RegExp("(^|&|\\?)" + name + "=([^&]*)(&|$)", "i");
    var param = window.location.href.substring(1).match(reg);
    if (param !== null) {
        return param[2];
    } else {
        return undefined;
    }
};

