/**
 * 
 * @param cb
 */
export function getConfig(cb) {
    require.ensure([], require => {
        cb(require('src/config'));
    }, 'config');
}
/**
 * sessionStorage
 * @param key
 * @return cb
 */
export function getItem(key) {
    var _result = JSON.parse(sessionStorage.getItem(key));
    return _result ? _result : false; //null
}
/**
 * sessionStorage
 * @param key
 * @return cb
 */
export function setItem(key, val) {
    sessionStorage.setItem(key, JSON.stringify(val));
}
/**
 * token
 * @param cb
 */

export function gettoken() {
    return new Promise((resolve, reject) => {
        let _result = getItem('token'); //
        if (!!_result) {
            resolve(_result);
        } else {
            getConfig(config => {
                Vue.http.get(config.token.gettoken + '/' + config.appid + '/' + config.appserect).then(res => {
                    res = res.body;
                    if (res.errcode === 0) {

                        setItem('token', res.data.token);
                        //    tokenajax
                        resolve(res.data.token);
                    } else {
                        reject()
                    }
                }, err => {
                    console.log(err);
                });
            })
        }
    });

}

